<template>
	<div class="cinema">
		<div class="top">
			<div class="box">
				<span>
					北京
				</span>
				<span @click="toSearch">
					搜影院
				</span>
			</div>
			<div class="list">
				<div class="navItem" 
					v-for="item,index in arr" 
					:class="{active : showIndex==index}"
					@click="handleChange(index)">
					{{item.title}}
				</div>
			</div>
		</div>
		
		<!--点击显示类似三级联动-->
		<div class="opactionBox" v-show="flag">
			
			<div class="topBox" @click="handle"></div>
			<div class="centerBox">
				<keep-alive>
					<component :is="opactions"></component>
				</keep-alive>
			</div>
			<div class="bottomBox" @click="handle"></div>
			
		</div>
		
		
		<div class="content_box" ref = "box">
			<div class="content">
				<div class="Item" v-for="item,index in cinemas">
					<div>
						<span>{{item.nm}}</span>
						<span>{{item.sellPrice}}元起</span>
						<span>{{item.addr}}</span>

					</div>
					<div>
						<span></span>
						<span></span>
					</div>
					<div>
						<span></span>
						<span></span>
					</div>
					<div>
						<span></span>
						<span></span>
					</div>
					
				</div>
			</div>

		</div>
		
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	import City from "./opaction/city.vue"
	import Brand from "./opaction/brand.vue"
	import Characteristic from "./opaction/Characteristic.vue"
	import BScroll from 'better-scroll'
	export default {
		name:"cinema",
		data() {
			return {
				page: 0,
				offset: 0,
				cinemas: [],
				showIndex:-1,
				flag:false,
				arr:[
				{
					title:"全城",
					path:"City"
				},
				{
					title:"品牌",
					path:"Brand"
				},
				{
					title:"特色",
					path:"Characteristic"
				}
				],
				opactions:"City"
			}
		},
		components:{
			City,
			Brand,
			Characteristic
		},
		methods: {
			async getData() {
				this.page++;

				let url = "/my/ajax/cinemaList?day=2019-01-17&offset=0&limit=20&districtId=-1&lineId=-1&hallType=-1&brandId=-1&serviceId=-1&areaId=-1&stationId=-1&item=&updateShowDay=true&reqId=1547696933712&cityId=10";
				if(this.page > 1) {
					var t = new Date().getTime();
					this.offset = (this.page - 1) * 20
					url = "/my/ajax/cinemaList?day=2019-01-17&offset=" + this.offset + "&limit=20&districtId=-1&lineId=-1&hallType=-1&brandId=-1&serviceId=-1&areaId=-1&stationId=-1&item=&updateShowDay=false&reqId=" + t + "&cityId=10"
				}
				await this.$http.get(url).then(res => {
					console.log(res)
					this.cinemas = this.cinemas.concat(res.data.cinemas)
				})

			},
			
			
			//跳转搜索
			toSearch(){
				this.$router.push("/search")
			},
			
			
			handleChange(index){
				this.showIndex=index
				this.opactions=this.arr[index].path
				this.flag=true;
				
				
			},
			handle(){
				if(this.showIdex!=0 || 1 || 2){
					this.flag=false
				}
			}
		},
		computed:{
			
		},
		created() {
			this.getData()
			//console.log(BScroll)
		},
		mounted() {
				this.scroll = new BScroll(this.$refs.box, {
					scrollX:false,
					scrollY:true,
					click:true,
					snapLoop: true,
					bounce: false
				})
				var that=this
				this.scroll.on('scrollEnd', function(params) {
					
					let dis = Math.abs(params.y);
					let viewHeight = that.$refs.box.clientHeight;
					//console.log(viewHeight)
					let h = that.$refs.box.children[0].clientHeight;
					if((dis + viewHeight) >= h) {
						that.getData();
						Toast({
						  message: '正在加载',
						   position: 'center',
						    duration: 500
						});
						
					}
				});
//				this.scroll.on('scrollStart', function(params) {
//					
//					  instance.close()
//				})
				
				//http://m.maoyan.com/ajax/filterCinemas?ci=10
				
		},
		updated(){
			this.scroll.refresh()
		}
	}
</script>

<style scoped>
	.active{
		color: red;
	}
	.cinema {
		width: 100%;
		border: 1px solid #fff;
	}
	
	.cinema .top {
		position: fixed;
		width: 100%;
		z-index: 222;
	}
	
	.cinema .box {
		width: 100%;
		height: 44px;
		line-height: 44px;
		display: flex;
		justify-content: flex-start;
		background: #f5f5f5;
	}
	
	.cinema .box span:nth-child(1) {
		display: block;
		width: 62px;
		height: 20px;
		padding-left: 15px;
	}
	
	.cinema .box span:nth-child(2) {
		display: block;
		width: 280px;
		height: 30px;
		line-height: 30px;
		border: 1px solid #000000;
		box-sizing: border-box;
		margin-top: 8px;
		padding-left: 18px;
		background: #fff;
	}
	
	.cinema .list {
		display: flex;
		justify-content: space-around;
		width: 100%;
		height: 40px;
		border-bottom: 1px solid #f5f5f5;
		background: #fff;
	}
	
	.cinema .list .navItem{
		width: 33%;
		height: 40px;
		text-align: center;
		line-height: 40px;
	}
	
	.content_box {
		margin-top: 84px;
		position: fixed;
		top:50px;
		bottom:50px;
		left:0;
		right:0
	}
	.Item{
		width: 343px;
		height: 145px;
		margin-left: 15px;
		padding: 13px 15px 13px 0;
	}
	
	/**/
	.opactionBox .topBox{
		position: fixed;
		top: 0px;
		width: 100%;
		height: 55px;
		z-index: 99;
		background:black;
		opacity: 0.3;
	}
	.opactionBox .centerBox{
		position: absolute;
		top: 80px;
		width: 100%;
		z-index: 100;
		background: #fff;
	}
	.opactionBox .bottomBox{
		position: fixed;
		bottom: 0px;
		width: 100%;
		height: 500px;
		z-index: 99;
		background:black;
		opacity: 0.3;
	}
</style>


