<template>
	<div class="box">
		<div class="content_box">
			<div class="left_box" id="left_box">
				<div class="left">
					<span v-for="item,index in subItems" :class="{A :show==index}" @click="twoList(item,index)">{{item.name}}({{item.count}})</span>
				</div>
			</div>
			<div class="right_box" id="right_box">
				<div class="right">
					<span v-for="item,index in secondList" @click="handleSend(item.id)">{{item.name}}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import BScroll from "better-scroll"
	export default {
		created() {
			this.getData();
			this.twoList
		},
		mounted() {
			this.scroll1 = new BScroll("#left_box", {
				scrollX: false,
				scrollY: true,
				click: true,
				bounce: false

			})
			this.scroll2 = new BScroll("#right_box", {
				scrollX: false,
				scrollY: true,
				click: true,
				bounce: false
			})
		},
		data() {
			return {
				subItems: [],
				secondList: [],
				show: -1
			}
		},

		methods: {
			getData() {
				this.$http.get("/my/ajax/filterCinemas?ci=10", {
					hearders:{
						'cache - control': "max-age=600",
						'content - encoding': "gzip",
						'content - type': "application/json; charset=utf-8",
						'date': "Sat, 19 Jan 2019 05:55:13 GMT",
						'server': "Tengine",
						'vary': "Accept-Encoding",
						'x - powered - by': "Express"
					}
				}).then(res => {
					console.log(res)
					this.subItems = res.data.district.subItems;

				})
			},
			twoList(item, index) {
				this.secondList = item.subItems;
				this.show = index
				//				console.log(this.secondList)
			},
			//发送地址
			handleSend(id){
//				this.$router.push({path:"/cinema/"+id})
			}
		}
	}
</script>

<style scoped>
	.A {
		color: red;
	}
	
	.box {
		background: #fff;
		z-index: 100000;
	}
	
	.box .content_box {
		width: 100%;
		display: flex;
		justify-content: flex-start;
	}
	
	.box .left_box {
		width: 125px;
		box-sizing: border-box;
		height: 400px;
		overflow: hidden;
		padding: 0 8px;
	}
	
	.box .content_box .left {
		width: 125px;
	}
	
	.box .content_box .left span {
		display: block;
		width: 100%;
		height: 44px;
		text-align: center;
		line-height: 44px;
	}
	/*右边*/
	
	.box .right_box {
		flex: 1;
		box-sizing: border-box;
		height: 400px;
		overflow: hidden;
		background: #D9CEC3;
	}
	
	.box .right_box .right {
		width: 100%;
	}
	
	.box .right_box .right span {
		width: 100%;
		display: block;
		height: 44px;
		line-height: 44px;
		text-align: center;
	}
</style>


