<template>
	<div class="city">
		<div class="box">
			<span v-for="item,index in arr" @click="handle(index)">{{item.title}}</span>
		</div>
		<keep-alive>
			<component :is="optaions"></component>
		</keep-alive>
	</div>
</template>

<script>
	import Sq from "./shangqu/shangqu"
	import Dt from "./shangqu/ditie"
	export default{
		name:"city",
		methods:{
			handle(index){
				this.showIndex=index;
				this.optaions=this.arr[index].path
			}
		},
		data(){
			return {
				arr:[
				{
					title:"商区",
					path:"Sq"
				},
				{
					title:"地铁站",
					path:"Dt"
				}
				],
				optaions:"Sq"
			}
		},
		components:{
			Sq,
			Dt
		}
	}
</script>

<style scoped>
	.city{
		width: 100%;
	}
	.box{
		width: 100%;
		display: flex;
		
	}
	.box span{
		display: block;
		width: 50%;
		line-height:30px ;
		text-align: center;
		
	}
</style>


